.markdown pre {
  background-color: #f8f8f8;
  padding: 0.2em 1em;
  padding-top: 8px;
  margin: 0.8em 0;
  padding-bottom: 6px;
  border: 1px solid #ddd;
  font-family: Consolas, "Liberation Mono", Courier, monospace;
  page-break-inside: avoid;
  text-align: left;
  overflow: visible;
  white-space: pre;
  border-radius: 4px;
}
.markdown pre code {
  display: block;
  word-wrap: break-word;
  white-space: pre-wrap;
  word-break: normal;
  border: none;
  padding: 0;
  font-size: 0.6rem;
}
.markdown li {
  list-style: disc;
}
.markdown li > ul > li {
  list-style: circle;
}
.markdown li > ul > li > ul > li {
  list-style: square;
}
.markdown code {
  padding: 0 4px;
  background: #f8f8f8;
  border: 1px solid #ddd;
  border-radius: 3px;
  font-size: 0.9rem;
}
